
import { api } from "@/utils/api.js";
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: "login",
props: {},
components: {
XButton
},
data() {
return {
phone: "",
code: "",
new_password1: "",
new_password2: "",
countdown: 60
};
},
created() {},
methods: {

getcode() {
let reg = /^1[0-9]{10,}$/; 
let _test = reg.test(this.phone);
if (_test) {
this.setcode();
} else {
this.$vux.toast.text("请输入正确的手机号码", "top");
}
},
async setcode() {
let that = this;
function settime() {
if (that.countdown == 0) {
that.countdown = 60;
that.flag = false;
} else {
that.countdown--;
setTimeout(function() {
settime();
}, 1000);
}
}

if (!this.flag) {
this.flag = true;
let timestamp = Date.parse(new Date()) / 1000;
let param = {
plat: 3,
tel: this.phone,
time: timestamp,
device_name: "web",
version: 1
};
let data = {
param: this.$encrypt(JSON.stringify(param))
};
const [err, res] = await api.findpwd_sms(data);
if (err) {
this.$vux.toast.text(err.msg);
this.flag = false;
return;
}
this.$vux.toast.text(res.msg);
settime();
} else {
console.log("函数防抖");
}
},

async checkInfo() {
let that = this;


















let data = {
tel: this.phone,
code: this.code,
pwd: this.$encrypt(this.new_password1.toString()),
review_pwd: this.$encrypt(this.new_password2.toString()),
plat: 3
};
if (!this._flag) {
this.$vux.loading.show({
text: "Loading"
});
this._flag = true;
const [err, res] = await api.getmemberinfo(data);
await setTimeout(() => { //节流
this._flag = false
}, 1000);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
this.$vux.toast.text(res.msg);
await setTimeout(() => {
that.$router.go(-1);
}, 500);
}else{
console.log('节流')
}
}
}
};



